* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

body {
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  color: #212121;
  background-color: #c9cce6;
  font-family: -apple-system, BlinkMacSystemFont, Segoe UI, Roboto, Oxygen, Ubuntu, Cantarell, Open Sans, Helvetica Neue, sans-serif;
  line-height: 1.5;
  overflow-x: hidden;
  overflow-y: scroll;
}

img {
  width: 100%;
  height: auto;
  display: block;
}

input {
  height: 40px;
  background-color: #c9cce6;
  border-radius: 5px;
  outline: 2px solid #422800;
  font-size: 15px;
}

button {
  color: #422800;
  cursor: pointer;
  text-align: center;
  -webkit-user-select: none;
  -ms-user-select: none;
  user-select: none;
  touch-action: manipulation;
  background-color: #c9cce6;
  border: 2px solid #422800;
  border-radius: 30px;
  padding: 0 18px;
  font-size: 18px;
  font-weight: 600;
  line-height: 40px;
  text-decoration: none;
  display: inline-block;
  box-shadow: 4px 4px #422800;
}

button:hover {
  background-color: #fff;
}

button:active {
  transform: translate(2px, 2px);
  box-shadow: 2px 2px #422800;
}

@media (min-width: 768px) {
  button {
    min-width: 120px;
    padding: 0 25px;
  }
}

#search-form {
  font: inherit;
  max-width: 320px;
}

.logo {
  width: 200px;
}

.search-container {
  border-radius: 5px;
  align-items: center;
  padding: 10px;
  display: flex;
}

.form-container {
  margin: 0 auto;
}

.gallery {
  flex-wrap: wrap;
  margin: 0 -20px -20px auto;
  padding: 10px;
  display: flex;
}

.photo-card {
  background: var(--white-font-color);
  border: 1px solid #eee;
  border-radius: 5px;
  flex-basis: calc(25% - 20px);
  margin-bottom: 20px;
  margin-right: 20px;
  padding: 5px;
  transition-property: box-shadow;
  transition-duration: .25s;
  transition-timing-function: cubic-bezier(.4, 0, .2, 1);
}

.photo-card:hover, .photo-card:focus {
  box-shadow: 0 1px 1px rgba(0, 0, 0, .12), 0 4px 4px rgba(0, 0, 0, .06), 1px 4px 6px rgba(0, 0, 0, .16);
}

.info {
  justify-content: space-between;
  display: flex;
}

info-item {
  justify-content: center;
  align-items: center;
  display: flex;
}

